import React, {useState, useEffect} from 'react'
import { Avatar, Button, CardHeader, Grid, InputAdornment, TextField, Typography} from "@material-ui/core";
import collection from 'crud/collections'
import TutorialDataService from 'services/TutorialService'

const ShowNest = (currentTutorial, setCurrentTutorial, thisCollection, isEdit) =>{

  const handleInputChange = event => {
    const { name, value } = event.target;
    setCurrentTutorial({ ...currentTutorial, [name]: value });
	console.log(currentTutorial, 'currenttu')
  };

  const updatePublished = status => {
    TutorialDataService.update(currentTutorial.id, { ...currentTutorial, published: status, updatedAt: new Date()})
      .then(response => {
         window.location.reload()
      })
      .catch(e => {
        console.log(e);
      });
  };

  const updateTutorial = () => {
    TutorialDataService.update(currentTutorial.id, currentTutorial)
      .then(response => {
		window.location.reload();
      })
      .catch(e => {
        console.log(e);
      });
  };

  const deleteTutorial = () => {
    TutorialDataService.remove(currentTutorial.id)
      .then(response => {
		window.location.reload();

      })
      .catch(e => {
        console.log(e);
      });
  };

	return (
		<div>	
		{Object.keys(collection[thisCollection].field).map((k, index)=>{
			let dataField=collection[thisCollection].field[k]
			  if (dataField.edit===true){
			   return (
				   <React.Fragment>
					<Grid container spacing={2}>
					<Grid item  xs={12} sm={12}>
					  {
						  typeof currentTutorial[k] === "object"?ShowNest(currentTutorial[k]):
						  (isEdit&&
						  <TextField 
							key={k} 
							label={dataField.show} 
							name={k}
							fullWidth
							defaultValue={currentTutorial[k]} 
							variant="outlined"
							onChange={handleInputChange}
							InputProps={{
								startAdornment: (
								<InputAdornment position="start">
									<Avatar> {dataField.icon}</Avatar>
								</InputAdornment>
								),
							}}
							/>
						  )
					  }
					  </Grid> 
					 </Grid>
					</React.Fragment>
			   )
		}})}
            {isEdit&&            
                <div>
                <Button  color="primary" onClick={deleteTutorial}>
                  Delete
                </Button>
                <Button  color="primary"
                  type="submit"
                  className="badge badge-success"
                  onClick={updateTutorial}
                >
                  Update
                </Button>
                </div>
            


            }
		</div>
)}
export default ShowNest